.box-preloader {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    // margin: auto;
    height: 100%;
    width: 100%;
    z-index: 99999;
    background-color: aquamarine;

    .preloader-logo {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        width: 100px;
        height: 100px;
    }
}

